body {
  margin: 0;
}

.progres-bar-container {
  background-color: #000c;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#progres-bar {
  width: 30%;
  height: 2%;
  margin-top: .5%;
}

label {
  color: #fff;
  font-size: 2rem;
}

.partIntro, .partExperience, .partSkill {
  color: #f9f1f0;
  opacity: 0;
  clip-path: polygon(0 43%, 2% 18%, 18% 0, 50% 9%, 82% 0, 98% 18%, 100% 41%, 93% 66%, 78% 84%, 50% 100%, 22% 84%, 7% 66%);
  background-color: brown;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 300px;
  padding: 5px 20px;
  animation: 2.5s forwards accordion;
  display: flex;
}

.partExperience, .partSkill {
  clip-path: none;
  border-radius: 20px;
}

.partIntro p, .partIntroMobile p {
  text-align: center;
  line-break: auto;
}

.partIntro > p > span > span, .partIntroMobile > p > span > span {
  padding-bottom: 10px;
  display: inline-block;
}

.partIntroMobile, .partExperienceMobile, .partSkillMobile {
  color: #f9f1f0;
  opacity: 0;
  background-color: brown;
  justify-content: center;
  align-items: center;
  padding: 5px 20px;
  display: flex;
}

.partApreciation {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

@keyframes accordion {
  from {
    height: 0;
  }

  to {
    height: 400px;
  }
}

@keyframes faded {
  from {
    opacity: .8;
    height: 400px;
  }

  to {
    opacity: 0;
    height: 0;
  }
}

@keyframes show {
  from {
    opacity: 0;
    height: 0;
  }

  to {
    opacity: .6;
    height: 400px;
  }
}

